import { Container, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core'; 

const useStyles = makeStyles({
    otsikko : {
        marginTop : 10,
        fontSize : 22,
        textAlign: "center"
    },
    alaotsikko : {
        marginTop : 10,
        fontSize : 18,
        textAlign: "center"
    },
    kappale : {
        marginTop : 10,
        fontSize : 14,
        textAlign: "center"
    }
});

function Aloitus() {

    const tyylit = useStyles();

    return (
        <Container>

            <Typography className={tyylit.otsikko}>
                Harjoitusty: TV-sarja-sovellus
            </Typography>

            <Typography className={tyylit.alaotsikko}>
                Sovellusohjelmointi 1
            </Typography>

            <Typography className={tyylit.kappale}>
                Tm React-projekti on Sovellusohjelmointi 1 -kurssin soveltava harjoitusty. Tyn sisltn on sovellus, jolla kyttj voi hakea API-rajapinnasta televisiosarjojen tietoja, list haluamansa sarjat omalle katsottavien sarjojen listalle, siirt katsotut omalle listalleen ja lopulta tyhjent katsottujen sarjojen listan.
            </Typography>
        </Container>
    )
}

export default Aloitus